<template>
  <div>
    <home-head></home-head>
    <div class="view-body">
      <h1>{{ msg }}</h1>
      <h2>Essential Links</h2>
    </div>
    <bottom-nav></bottom-nav>
  </div>
</template>

<script>

import HomeHead from '../components/HomeHead'
import BottomNav from '../components/BottomNav'
export default {
  name: 'home',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  },

  components: {
    HomeHead,
    BottomNav
  },

  mounted() {
    this.init();
  },

  methods: {
    init() {
      let map = new Map();
      map.set('a', 'apple');
      map.set('b', 'orange');
      // console.log(map);
      // console.log(map.get('b'));
      // console.log(map.size);
      map.delete('a');
      // console.log(map);
      // console.log(map.size)
      // console.log(map.entries())
      map.set('c', 'pineapple');

      // for (let name of map) {
      //   console.log(name)
      // }

      // for (let key of map.keys()) {
      //   console.log(key)
      // }

      // for (let [kay, value] of map.entries()) {
      //   console.log(key, value)
      // }

      const sum = (a, b) => {
        return a+b;
      }

      const m = sum(1,2);
      // console.log(m);

      const o = param => {
        console.log(param)
        console.log(arguments[0]) //注意：箭头函数下 ，arguments 不能使用了
      }

      // o('hello');

      var oo = function(param) {
        console.log(arguments[0])
      }

      // oo('88')


      const p = () => {
        console.log('no arguments')
      };

      // p()

      class Person  {
        constructor(name, age=23) {
          this.name = name;
          this.age = age;
        }
        showName() {
          return this.name;
        }
        showAge() {
          return this.age;
        }
      }

      const p1 = new Person('Lanch', 88);
      // console.log(p1.showName())

      class Manger extends Person {
        constructor(name, age, job="manger") {
          super(name, age);
          this.job = job;
        }
        showJob() {
          return this.job;
        }
      }

      const p2 = new Manger('Ladu', 22);
      // console.log(p2.showJob());
      // console.log(p2.showAge())

    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



